"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import SearchBox from "@/components/SearchBox";
import AnimeCard from "@/components/AnimeCard";

interface Anime {
  mal_id: number;
  title: string;
  images: {
    jpg: {
      image_url: string;
    };
  };
  score?: number;
  type?: string;
  episodes?: number;
}

interface Pagination {
  current_page: number;
  last_visible_page: number;
  has_next_page: boolean;
  items: {
    total: number;
  };
}

export default function Page() {
  const router = useRouter();
  const [items, setItems] = useState<Anime[]>([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [term, setTerm] = useState("");
  const [loading, setLoading] = useState(false);
  const [showBackButton, setShowBackButton] = useState(false);

  // Fun√ß√£o para carregar animes
  async function loadItems(searchTerm: string, page: number) {
    if (loading) return;
    
    setLoading(true);
    try {
      const encodedTerm = encodeURIComponent(searchTerm.trim());
      
      let url: string;
      if (searchTerm && searchTerm.trim() !== "") {
        url = `https://api.jikan.moe/v4/anime?q=${encodedTerm}&page=${page}&limit=24`;
      } else {
        url = `https://api.jikan.moe/v4/seasons/now?page=${page}&limit=24`;
      }

      console.log(`Buscando: "${searchTerm}" | P√°gina: ${page}`);

      // Delay para evitar rate limit
      await new Promise(resolve => setTimeout(resolve, 500));
      
      const res = await fetch(url);
      
      if (!res.ok) {
        if (res.status === 429) {
          // Aguardar 3 segundos e tentar novamente
          await new Promise(resolve => setTimeout(resolve, 3000));
          return loadItems(searchTerm, page);
        }
        throw new Error(`Erro HTTP: ${res.status}`);
      }
      
      const json = await res.json();
      const data: Anime[] = json.data || [];
      const pagination: Pagination = json.pagination || {
        current_page: 1,
        last_visible_page: 1,
        has_next_page: false,
        items: { total: 0 }
      };

      console.log(`Resultados: ${data.length} | Total: ${pagination.items.total} | P√°ginas: ${pagination.last_visible_page}`);

      setItems(data);
      setTotalPages(pagination.last_visible_page);
      setCurrentPage(pagination.current_page);

      // Salvar termo se for uma busca real
      if (searchTerm && searchTerm.trim() !== "") {
        localStorage.setItem("lastAnimeSearch", searchTerm);
        setShowBackButton(true);
      }

    } catch (error) {
      console.error("Erro na busca:", error);
      setItems([]);
    } finally {
      setLoading(false);
    }
  }

  // Inicializa√ß√£o
  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const urlQuery = urlParams.get('q');
    
    if (urlQuery && urlQuery.trim() !== "") {
      setTerm(urlQuery);
      setShowBackButton(true);
      loadItems(urlQuery, 1);
    } else {
      const savedTerm = localStorage.getItem("lastAnimeSearch");
      if (savedTerm && savedTerm.trim() !== "") {
        setTerm(savedTerm);
        setShowBackButton(true);
        loadItems(savedTerm, 1);
      } else {
        loadItems("", 1);
      }
    }
  }, []);

  // Fun√ß√£o de busca
  function handleSearch(newTerm: string) {
    setTerm(newTerm);
    setCurrentPage(1);
    setTotalPages(1);
    
    // Limpar URL
    if (typeof window !== 'undefined') {
      window.history.pushState({}, '', '/search');
    }
    
    loadItems(newTerm, 1);
  }

  // Navegar para p√°gina
  function goToPage(page: number) {
    if (page < 1 || page > totalPages || loading) return;
    setCurrentPage(page);
    loadItems(term, page);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  }

  // Limpar busca
  function handleClearSearch() {
    setTerm("");
    setCurrentPage(1);
    setItems([]);
    setTotalPages(1);
    setShowBackButton(false);
    localStorage.removeItem("lastAnimeSearch");
    loadItems("", 1);
  }

  // Gerar array de p√°ginas para mostrar
  function getPageNumbers() {
    const pages = [];
    const maxPagesToShow = 5;
    
    let startPage = Math.max(1, currentPage - Math.floor(maxPagesToShow / 2));
    let endPage = Math.min(totalPages, startPage + maxPagesToShow - 1);
    
    // Ajustar se estiver perto do in√≠cio
    if (endPage - startPage + 1 < maxPagesToShow) {
      startPage = Math.max(1, endPage - maxPagesToShow + 1);
    }
    
    for (let i = startPage; i <= endPage; i++) {
      pages.push(i);
    }
    
    return pages;
  }

  return (
    <div
      style={{
        width: "100vw",
        minHeight: "100vh",
        backgroundImage: "url('/scenario.png')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        padding: 20,
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
      }}
    >
      {/* Bot√£o voltar */}
      <div style={{ width: "100%", maxWidth: "1000px", marginBottom: "20px" }}>
        <button
          onClick={() => router.push('/')}
          style={{
            padding: "10px 20px",
            background: "rgba(0, 0, 0, 0.6)",
            color: "white",
            border: "none",
            borderRadius: "8px",
            cursor: "pointer",
            fontWeight: "bold",
            fontSize: "14px",
            display: "flex",
            alignItems: "center",
            gap: "8px",
            transition: "all 0.3s",
            border: "1px solid #4ECDC4"
          }}
          onMouseEnter={(e) => {
            e.currentTarget.style.background = "rgba(78, 205, 196, 0.3)";
          }}
          onMouseLeave={(e) => {
            e.currentTarget.style.background = "rgba(0, 0, 0, 0.6)";
          }}
        >
          üè† Voltar para Home
        </button>
      </div>

      <h1 style={{ color: "white", marginBottom: 20 }}>Buscar Animes</h1>

      <SearchBox onSearch={handleSearch} />

      {/* Informa√ß√µes da busca */}
      {term && term.trim() !== "" && (
        <div style={{
          width: "100%",
          maxWidth: "1000px",
          background: "rgba(0, 0, 0, 0.8)",
          borderRadius: "10px",
          padding: "15px 20px",
          marginBottom: "20px",
          marginTop: "20px",
          border: "2px solid #1e88e5",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          flexWrap: "wrap",
          gap: "10px"
        }}>
          <div style={{ color: "white" }}>
            <div style={{ 
              display: "flex", 
              alignItems: "center", 
              gap: "10px",
              marginBottom: "5px"
            }}>
              <span style={{ fontSize: "20px" }}>üîç</span>
              <span style={{ fontSize: "16px", fontWeight: "bold" }}>
                Resultados para: <span style={{ color: "#90caf9" }}>{term}</span>
              </span>
            </div>
            <div style={{ 
              fontSize: "14px", 
              color: "#aaa",
              display: "flex",
              alignItems: "center",
              gap: "10px"
            }}>
              <span>üìÑ P√°gina {currentPage} de {totalPages}</span>
              <span>‚Ä¢</span>
              <span>üé¨ {items.length} animes nesta p√°gina</span>
            </div>
          </div>
          
          <button
            onClick={handleClearSearch}
            style={{
              padding: "8px 16px",
              background: "#ff4444",
              color: "white",
              border: "none",
              borderRadius: "6px",
              cursor: "pointer",
              fontWeight: "bold",
              fontSize: "14px",
              display: "flex",
              alignItems: "center",
              gap: "5px",
              transition: "all 0.3s"
            }}
            onMouseEnter={(e) => {
              e.currentTarget.style.background = "#cc0000";
              e.currentTarget.style.transform = "scale(1.05)";
            }}
            onMouseLeave={(e) => {
              e.currentTarget.style.background = "#ff4444";
              e.currentTarget.style.transform = "scale(1)";
            }}
          >
            <span>üóëÔ∏è</span>
            <span>Limpar Busca</span>
          </button>
        </div>
      )}

      {/* Loading */}
      {loading && (
        <div style={{ marginTop: 40, textAlign: "center" }}>
          <div style={{
            width: "50px",
            height: "50px",
            border: "5px solid rgba(255,255,255,0.1)",
            borderTop: "5px solid #1e88e5",
            borderRadius: "50%",
            margin: "0 auto 15px",
            animation: "spin 1s linear infinite"
          }} />
          <p style={{ color: "white" }}>Carregando animes...</p>
        </div>
      )}

      {/* Grid de animes */}
      {!loading && items.length > 0 && (
        <>
          <div
            style={{
              display: "grid",
              gridTemplateColumns: "repeat(auto-fill, minmax(180px, 1fr))",
              gap: 20,
              width: "100%",
              maxWidth: 1000,
              marginTop: "20px",
            }}
          >
            {items.map((anime) => (
              <div
                key={anime.mal_id}
                onClick={() => {
                  localStorage.setItem("lastAnimeSearch", term);
                  router.push(`/${anime.mal_id}`);
                }}
                style={{ cursor: "pointer" }}
              >
                <AnimeCard anime={anime} />
              </div>
            ))}
          </div>

          {/* Pagina√ß√£o */}
          {totalPages > 1 && (
            <div style={{
              marginTop: "40px",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              gap: "10px",
              flexWrap: "wrap"
            }}>
              {/* Bot√£o p√°gina anterior */}
              <button
                onClick={() => goToPage(currentPage - 1)}
                disabled={currentPage === 1 || loading}
                style={{
                  padding: "10px 20px",
                  background: currentPage === 1 ? "#555" : "#1e88e5",
                  color: "white",
                  border: "none",
                  borderRadius: "6px",
                  cursor: currentPage === 1 ? "not-allowed" : "pointer",
                  fontWeight: "bold",
                  fontSize: "16px",
                  opacity: currentPage === 1 ? 0.5 : 1
                }}
              >
                ‚Üê Anterior
              </button>

              {/* Primeira p√°gina */}
              {currentPage > 3 && (
                <>
                  <button
                    onClick={() => goToPage(1)}
                    style={{
                      padding: "10px 16px",
                      background: "rgba(255, 255, 255, 0.1)",
                      color: "white",
                      border: "none",
                      borderRadius: "6px",
                      cursor: "pointer",
                      fontWeight: "bold"
                    }}
                  >
                    1
                  </button>
                  {currentPage > 4 && <span style={{ color: "white" }}>...</span>}
                </>
              )}

              {/* P√°ginas do meio */}
              {getPageNumbers().map(pageNum => (
                <button
                  key={pageNum}
                  onClick={() => goToPage(pageNum)}
                  style={{
                    padding: "10px 16px",
                    background: currentPage === pageNum ? "#4ECDC4" : "rgba(255, 255, 255, 0.1)",
                    color: "white",
                    border: currentPage === pageNum ? "2px solid #4ECDC4" : "none",
                    borderRadius: "6px",
                    cursor: "pointer",
                    fontWeight: "bold",
                    minWidth: "45px"
                  }}
                >
                  {pageNum}
                </button>
              ))}

              {/* √öltima p√°gina */}
              {currentPage < totalPages - 2 && (
                <>
                  {currentPage < totalPages - 3 && <span style={{ color: "white" }}>...</span>}
                  <button
                    onClick={() => goToPage(totalPages)}
                    style={{
                      padding: "10px 16px",
                      background: "rgba(255, 255, 255, 0.1)",
                      color: "white",
                      border: "none",
                      borderRadius: "6px",
                      cursor: "pointer",
                      fontWeight: "bold"
                    }}
                  >
                    {totalPages}
                  </button>
                </>
              )}

              {/* Bot√£o pr√≥xima p√°gina */}
              <button
                onClick={() => goToPage(currentPage + 1)}
                disabled={currentPage === totalPages || loading}
                style={{
                  padding: "10px 20px",
                  background: currentPage === totalPages ? "#555" : "#1e88e5",
                  color: "white",
                  border: "none",
                  borderRadius: "6px",
                  cursor: currentPage === totalPages ? "not-allowed" : "pointer",
                  fontWeight: "bold",
                  fontSize: "16px",
                  opacity: currentPage === totalPages ? 0.5 : 1
                }}
              >
                Pr√≥xima ‚Üí
              </button>
            </div>
          )}

          {/* Informa√ß√£o de p√°ginas */}
          {totalPages > 1 && (
            <div style={{
              marginTop: "20px",
              color: "white",
              textAlign: "center",
              fontSize: "14px",
              background: "rgba(0, 0, 0, 0.5)",
              padding: "10px 20px",
              borderRadius: "8px"
            }}>
              <span>üìã P√°gina {currentPage} de {totalPages}</span>
              <span style={{ margin: "0 15px" }}>‚Ä¢</span>
              <span>üé¨ {items.length} animes por p√°gina</span>
              <span style={{ margin: "0 15px" }}>‚Ä¢</span>
              <span>üî• Total de p√°ginas: {totalPages}</span>
            </div>
          )}
        </>
      )}

      {/* Nenhum resultado */}
      {!loading && items.length === 0 && term && (
        <div style={{
          marginTop: 40,
          padding: 30,
          background: "rgba(0, 0, 0, 0.7)",
          borderRadius: "12px",
          textAlign: "center",
          maxWidth: "600px",
          width: "100%"
        }}>
          <div style={{ fontSize: "48px", marginBottom: "10px" }}>üòï</div>
          <h3 style={{ color: "white", marginBottom: "10px" }}>
            Nenhum anime encontrado
          </h3>
          <p style={{ color: "#aaa", marginBottom: "20px" }}>
            N√£o encontramos resultados para "<strong style={{ color: "#90caf9" }}>{term}</strong>".
            Tente usar termos diferentes ou verifique a ortografia.
          </p>
          <button
            onClick={handleClearSearch}
            style={{
              padding: "10px 20px",
              background: "#4ECDC4",
              color: "white",
              border: "none",
              borderRadius: "6px",
              cursor: "pointer",
              fontWeight: "bold"
            }}
          >
            Ver Animes da Temporada
          </button>
        </div>
      )}

      {/* Estado inicial (sem busca) */}
      {!loading && items.length === 0 && !term && (
        <div style={{
          marginTop: 40,
          padding: 30,
          background: "rgba(0, 0, 0, 0.7)",
          borderRadius: "12px",
          textAlign: "center",
          maxWidth: "600px",
          width: "100%"
        }}>
          <div style={{ fontSize: "48px", marginBottom: "10px" }}>üîç</div>
          <h3 style={{ color: "white", marginBottom: "10px" }}>
            Busque seu anime favorito
          </h3>
          <p style={{ color: "#aaa" }}>
            Digite o nome de um anime na barra de busca acima para come√ßar.
          </p>
        </div>
      )}

      <style jsx>{`
        @keyframes spin {
          0% { transform: rotate(0deg); }
          100% { transform: rotate(360deg); }
        }
      `}</style>
    </div>
  );
}